<template>
<v-container text-xs-center>
  <v-layout row wrap>
    <v-flex md7 sm12 xs12 class="left">
      <div class="title">10 Lessones Options</div>
      <div class="fee-box">
        <div class="fee-card charge" v-for="item in classes" :key="item.plan" v-if="!item.isFree">
          <h2>{{ item.period }}</h2>
          <h3>{{ item.time }}</h3>
          <v-btn depressed large class="price">{{ item.price }}</v-btn>
          <v-btn depressed large class="en">{{item.btn}}</v-btn>
        </div>
      </div>
      <br>
      <div class="title">Other Options (Less than 10 lessons)</div>  
      <div class="fee-box">
        <div class="fee-card free" v-for="item in classes" :key="item.plan" v-if="item.isFree">
          <h2>{{ item.period }}</h2>
          <h3>{{ item.time }}</h3>
          <v-btn depressed large class="price">{{ item.price }}</v-btn>
          <v-btn depressed large class="en">{{item.btn}}</v-btn>
        </div>
      </div>   
    </v-flex>
    <v-flex md5 sm12 xs12 class="right"></v-flex>
  </v-layout>
</v-container>
</template>

<script>

export default {
  data () {
    return {
      classes: [
        {
          plan: 1,
          isFree: false,
          period: '1H / week',
          time: '10 lessons',
          price: '$599',
          btn: 'Enroll Now'
        },
        {
          plan: 2,
          isFree: false,
          period: '1.5H / week',
          time: '10 lessons',
          price: '$898',
          btn: 'Enroll Now'
        },
        {
          plan: 3,
          isFree: false,
          period: '2H / week',
          time: '10 lessons',
          price: '$1198',
          btn: 'Enroll Now'
        },
        {
          plan: 4,
          isFree: true,
          period: '1 lesson',
          time: '',
          price: '$65',
          btn: 'Enroll Now'
        },
        {
          plan: 5,
          isFree: true,
          period: '1 trail lesson',
          time: '',
          price: '$55',
          btn: 'Enroll Now'
        }
      ]
    }
  }
}
</script>

<style lang="sass" scoped>
@import ../assets/sass/juice
.left
  .title
    width: 100%
    padding: 20px

    background: #B197F6
    border-radius: 20px

  .fee-box
    margin-top: 1rem
    display: flex
    justify-content: space-between
    flex-wrap: wrap 
    .fee-card
      display: flex
      flex-direction: column   
      background: #111
      color: white
      border-radius: 20px
      background: #688AD3
      padding: 1.2rem 0 1.2rem 0
      button
        border-radius: 10px
      .price
        font-size: 2rem
      .price, .en
        background: #69D39E
        color: white
    .charge
      width: 100%
      +bp(small-up)
        width: 32%  
    .free
      width: 100%
      +bp(small-up)
        width: 49%  
.right

</style>


