  <template>
  <v-app>
    <v-navigation-drawer
      persistent
      :mini-variant="miniVariant"
      :clipped="clipped"
      v-model="drawer"
      enable-resize-watcher
      fixed
      app
    >
      <v-list>
        <router-link v-for="(item, i) in items" :key="i" :to=item.link>
          <v-list-tile value="true">
            <v-list-tile-action>
              <v-icon v-html="item.icon"></v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title v-text="item.title"></v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </router-link>
      </v-list>
    </v-navigation-drawer>

    <v-toolbar
      app
      :clipped-left="clipped"
      :height="height"
      color="cyan lighten-3"
      flat
      dark
    >
      <router-link to="/" exact><v-toolbar-title class="title" v-text="title"></v-toolbar-title></router-link>
      <!-- <v-btn flat dark router to="/">{{ title }}</v-btn> -->
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-md-and-down">
        <v-btn flat dark router v-for="item in items" :key="item.title" :to="item.link">
          {{ item.title }}
        </v-btn>
      </v-toolbar-items>

      <v-toolbar-side-icon @click.stop="drawer = !drawer" class="hidden-lg-and-up" dark></v-toolbar-side-icon>
    </v-toolbar>

    <v-content>
      <router-view/>
    </v-content>

    <v-footer :fixed="fixed" app>
      <span>&copy; 2018</span>
    </v-footer>
  </v-app>
</template>

<script>
export default {
  data () {
    return {
      miniVariant: false,
      clipped: false,
      height: 40,
      drawer: false,
      fixed: true,
      flags: [
        {
          name: 'Korean',
          code: 'kr'
        },
        {
          name: 'Chinese',
          code: 'cn'
        },
        {
          name: 'English',
          code: 'gb'
        },
        {
          name: 'French',
          code: 'fr'
        },
        {
          name: 'Thai',
          code: 'th'
        },
        {
          name: 'Japanese',
          code: 'jp'
        },
        {
          name: 'Spanish',
          code: 'es'
        },
        {
          name: 'Russian',
          code: 'ru'
        },
        {
          name: 'Indonesian',
          code: 'id'
        }
      ],
      items: [
        {
          icon: 'language',
          title: 'Language',
          link: '/language'
        },
        {
          icon: 'book',
          title: 'Programmes',
          link: '/programmes'
        },
        {
          icon: 'group',
          title: 'About Us',
          link: '/about'
        },
        {
          icon: 'star',
          title: 'Teaching Style',
          link: '/teachingstyle'
        },
        {
          icon: 'perm_phone_msg',
          title: 'Contact',
          link: '/contact'
        }
      ],
      title: 'Yume Language School'
    }
  },
  name: 'App'
}
</script>

<style lang="sass" scoped>
@import ./assets/sass/juice
a
  color: #424242
  text-decoration: none
  div
    color: #424242

.title
  color: #fff

.fluid
  padding: 0

</style>



