<template>
  <div class="main">
    <div class="form">
      <div class="left">
        <div class="pic-box">
          <div class="pic">
            <flag iso="cn" title="Chinese" squared/>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="name">Franda Sun</div> 
        <div>
          <v-icon small>calendar_today</v-icon>
          <span>DD/MM/YY   ~  DD/MM/YY</span>
          <span class="week">
            <span class="day available">SUN</span>
            <span class="day available">MON</span>
            <span class="day available">TUE</span>
            <span class="day available">WED</span>
            <span class="day">THU</span>
            <span class="day">FRI</span>
            <span class="day available">SAT</span>
          </span>
        </div>
        <div>
          <v-icon size="18px">location_on</v-icon>
          <span>CBD, Lower Hutt, etc…</span>
        </div>
        <v-btn v-if="!isShow" class="small-btn" small flat v-on:click="isShow = !isShow">read more</v-btn>
        <v-btn v-if="isShow" class="small-btn" small flat v-on:click="isShow = !isShow">hide detail</v-btn>
      </div>
    </div>
    <div class="detail" v-if="isShow">
      <p>Master in Applied Linguistics/TESOL.</p>
      <p>Obtained over three years experience in language teaching (English/Mandarin) and assessment.</p>
      <p>Rich teaching experience with language beginners, adults learners and low literacy learners.</p>
      <p>Working on the exploration of an entertaining way of language learning, enjoying using songs to motivate learning and enhance language acquisition
      <p>Working on creating a comfortable and inclusive learning environment for the students.</p>
      <p>Aiming at developing students’ critical thinking and raising students’ language awareness of the target language.</p>
      <br>
      <h5>Research interest:</h5>
      <p>genre analysis, Language for specific purposes, language speaking and listening, vocabulary</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Teacher',
  data () {
    return {
      isShow: false
    }
  }
}
</script>

<style lang="sass" scoped>
@import ../assets/sass/juice
.main
  &:hover
    .form, .detail
      -webkit-box-shadow: 0px 2px 3px -1px rgba(130,130,130,1)
      -moz-box-shadow: 0px 2px 3px -1px rgba(130,130,130,1)
      box-shadow: 0px 2px 3px -1px rgba(130,130,130,1)
  .form
    display: flex
    align-items: flex-end
    margin-top: 80px
    background: #80DEEA
    transition: all 0.3s ease
    border-radius: 50px 25px 50px 25px

    .left
      .pic-box
        width: 150px
        background: black
        position: relative
        .pic
          position: absolute
          border-bottom-left-radius: 25px
          bottom: 0
          +bp(max, 515px)
            top: -200px
            bottom: auto
          +size(150px, 200px)
          background:
            image: url('../assets/teacher/Franda.png')
            size: cover
          span
            +size(50px)
            border-radius: 50%
            margin-right: 1.2rem
            position: absolute
            bottom: 0
            &:after
              position: absolute
              content: 'Mandarin'
              color: white
              left: 60px
              bottom: 10px

    .right
      box-sizing: border-box
      display: flex
      flex-direction: column
      padding: 1rem 1rem 0 2rem

      .name
        font-size: 1.5rem
        font-weight: bold
        margin-bottom: 0.4rem
        transform: translateX(5px)
      div
        margin-bottom: 0.4rem
        margin-left: 2.1rem
        color: #333
        i
          transform: translateY(-1px)
          width: 30px
          text-align: center
        .week
          margin: 0.6rem 0 4px 2.3rem
          box-sizing: border-box
          .day
            border: 1px solid #333
            margin: 0
            +size(30px, 19px)
            text-align: center
            font-size: 10px
            line-height: 18px
          .available
            background: #64FFDA

      .btn
        width: 30px
        color: #6a55a2
        transform: translateX(-6px)
        margin: 0 0 10px 2.4rem
  .detail
    width: 100%
    margin-top: .2rem
    background: #7BD8A9
    border-radius: 25px 50px 25px 50px
    box-sizing: border-box
    padding: 3rem
</style>


