<template>


  <v-container fluid>
    <h1>Yume Language School</h1>
    <v-layout wrap row>
      <v-spacer/>
      <v-flex v-for="flag in flags" :key="flag.name" xs4 sm2 md1 align-center>
        <router-link to="/country" class="flagbox">
          <flag :iso="flag.code" :title="flag.name" squared/>
          <div>{{ flag.name }}</div>
        </router-link>
      </v-flex>
    </v-layout>
    <v-carousel interval="2500">
      <v-carousel-item v-for="(item,i) in items" :src="item.src" :key="i"></v-carousel-item>
    </v-carousel>
    <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Qui voluptatem voluptatibus distinctio alias soluta itaque eos aspernatur corporis libero accusantium reprehenderit ad vel autem esse nobis, earum facere, voluptatum perspiciatis.</p>
    <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Qui voluptatem voluptatibus distinctio alias soluta itaque eos aspernatur corporis libero accusantium reprehenderit ad vel autem esse nobis, earum facere, voluptatum perspiciatis.</p>
    <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Qui voluptatem voluptatibus distinctio alias soluta itaque eos aspernatur corporis libero accusantium reprehenderit ad vel autem esse nobis, earum facere, voluptatum perspiciatis.</p>
    <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Qui voluptatem voluptatibus distinctio alias soluta itaque eos aspernatur corporis libero accusantium reprehenderit ad vel autem esse nobis, earum facere, voluptatum perspiciatis.</p>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        items: [
          {
            src: 'static/doc-images/carousel/chinese.jpg'
          },
          {
            src: 'static/doc-images/carousel/japanese.jpg'
          },
          {
            src: 'static/doc-images/carousel/french.jpg'
          },
          {
            src: 'static/doc-images/carousel/korean.jpg'
          }
        ],
        flags: [
          {
            name: 'Korean',
            code: 'kr'
          },
          {
            name: 'Chinese',
            code: 'cn'
          },
          {
            name: 'English',
            code: 'gb'
          },
          {
            name: 'French',
            code: 'fr'
          },
          {
            name: 'Thai',
            code: 'th'
          },
          {
            name: 'Japanese',
            code: 'jp'
          },
          {
            name: 'Spanish',
            code: 'es'
          },
          {
            name: 'Russian',
            code: 'ru'
          },
          {
            name: 'Indonesian',
            code: 'id'
          }
        ]
      }
    }
  }
</script>

<style lang="sass" scoped>
.flagbox
  display: flex
  flex-direction: column
  justify-content: center
  text-decoration: none
  align-items: center
  color: #424242
  margin-bottom: 1.2rem
  &:hover
    .flag-icon
      box-shadow: 1px 1px 7px 0 #00BCD4
    div
      color: #00838F
      &:after
        width: 100%
  .flag-icon
    width: 30px
    height: 30px
    border-radius: 50%
    transition: all 0.6s ease
  div
    transition: all 0.6s ease
    position: relative
    margin-top: 0.3rem
    &:after
      content: ''
      display: block
      position: absolute
      left: 50%
      transform: translateX(-50%)
      width: 0
      height: 1px
      background: #00BCD4
      transition: all 0.4s ease

</style>




